<template>
	<div class="page1_3_4">
		<div class="am-contain content box-border-shadow">
			<div class="find">
				<div style="margin-top: 15px;width: 40%;margin: 0 auto">
				  <el-input placeholder="请输入关键词" v-model="findWord" class="input-with-select">
				  	<el-select slot="prepend" v-model="selectKey">
					    <el-option
					      v-for="(item,key) in keys"
					      :key="key"
					      :label="key"
					      :value="key">
					    </el-option>
					</el-select>
				    <el-button slot="append" icon="el-icon-search" @click="getData(findWord)"></el-button>
				  </el-input>
				  <div class="word_main">
				  	<ul>
				  		<li>推荐:</li>
				  		<li v-for="item in keys[selectKey]" class="item" @click="getData(item)">{{item}}</li>
				  		<!-- <li class="item">南京审计大学</li>
				  		<li class="item" @click="getData(this)">教育部</li>
				  		<li class="item" @click="getData(this)">商学院</li>
				  		<li class="item" @click="getData(this)">信息工程学院</li>
				  		<li class="item" @click="getData(this)">审计署</li> -->
				  	</ul>
				  </div>
				</div>
	        </div>
	        <ul class="fapiaoList">
	        	<li v-if="dataList=='正在加载'">
	        		<div style="margin: 40px auto;font-size: 40px;text-align: center;"><i class="am-icon-spinner am-icon-spin"></i> 正在加载.....</div>
	        	</li>
	        	<li v-else-if="dataList=='数据不存在'">
	        		<div style="margin: 40px auto;font-size: 40px;text-align: center;">数据不存在</div>
	        	</li>
	            <li v-else class="item box-border-shadow" v-for="(obj,index) in dataList" :style="{'background-image':'url('+(index%3==0?photoSrc:index%2==0?photoSrc2:photoSrc3)+')'}">
	            	<div v-for="(item,key,index) in obj" v-if="index<10" style="overflow: hidden;white-space: nowrap;text-overflow: ellipsis;">
	            		  <span>{{key}}: </span> <span :title="item">{{item}}</span>
	            	</div>
	            	<div class="xq_btn" data-am-modal="{target: '#doc-modal-1', closeViaDimmer: 0}" @click="fpxq=obj">查看详情</div>
	            </li>                                                                
	        </ul>
		</div>
		<div class="am-modal am-modal-no-btn" tabindex="-1" id="doc-modal-1">
		  <div class="am-modal-dialog">
		    <div class="am-modal-hd" style="height: 44px;line-height: 44px">{{fpxq['发票名称']}}
		      <a href="javascript: void(0)" class="am-close am-close-spin" data-am-modal-close>&times;</a>
		    </div>
		    <div class="am-modal-bd" style="overflow-y: auto;max-height: 500px;text-align: left;padding: 30px;box-sizing: border-box;">
		      	<div v-for="(item,key,index) in fpxq">
        		  <span style="color: #b80000">{{key}}: </span> <span>{{item}}</span>
        		</div>
		    </div>
		  </div>
		</div>
	</div>
</template>
<script type="text/javascript">
	import reqData from "../../../reqData.js"
	export default{
		name:'page1_3_4',
		data(){
			return {
				findWord:'',
				photoSrc:require('../../../assets/photo-cut/1.png'),
				photoSrc2:require('../../../assets/photo-cut/2.jpg'),
				photoSrc3:require('../../../assets/photo-cut/3.jpg'),
				dataList:'正在加载',
				keys:{'发票名称':['山东增值税电子普通发票','湖北增值税电子普通发票','广东增值税电子普通发票'],'发票代码':['037001700112','042001700112','043001800112'],'发票号码':['13208805','36151707','00006589'],'开票时间':['2018年03月23日']},
				selectKey:'发票名称',
				fpxq:{}

			}
		},
		components:{
		},
		mounted: function(){
			 this.getData('空')
		},
		methods:{
			getData(word){
			  	let _this=this;
			  	_this.dataList='正在加载';
			   	reqData
			        .req({
			          apiName: "Neo4jToInvioce",
			           data: {             
			             word:word
			           }
			        })
			        .then(res => {
			          res = res.data;
			          if (res && res.code === "200") {
			            let data = res.data;
			            console.log(data);
			            if(data.length>0){
			            	_this.dataList=data;
			            }else{
			            	_this.dataList="数据不存在";
			            }
			            
			          }
			        })
			        .catch(err => {
			          console.log(err);
			       });
			},
		}
	}
</script>
<style scoped="">
	.content{
	    margin: 0 auto;
	    width: 100%;
	    padding:20px 0;
	    box-sizing: border-box;
	}
	.fapiaoList{
		width: auto;
		margin: 0 auto;
		overflow: hidden;
		padding: 20px;
		box-sizing: border-box;
	}
	.fapiaoList>.item{
		float: left;
		margin: 1%;
		display: block;
		padding: 10px;
		box-sizing: border-box;
		width: 23%;
	    min-width: 300px;
	    height: 300px;
	    background-color: #fff;
	    background-position: center;
	    background-size: contain;
	    background-repeat: no-repeat;
	    color: transparent;
	    
	}
	.fapiaoList>.item:hover{
		background-image: none !important;
		color: #fff;
		background-color: rgba(154,2,2,0.5);
	}
	.fapiaoList>.item .xq_btn{
		margin: 0 auto;
		width: 120px;
		text-align: center;
		height: 40px;
		line-height: 40px;
		border:1px solid #fff;
		border-radius: 6px;
		color: #fff;
		margin-top: 8px;
		cursor: pointer;
	}
	.fapiaoList>.item .xq_btn:hover{
		background-color: #fff;
		color: #b80000;
	}
	
	.block{
		text-align: center;
		margin-top: 30px;
		margin-bottom: 80px;
	}
	.word_main{
		margin: 20 auto;
		overflow: hidden;
	}
	.word_main li{
		float: left;
		line-height: 40px;
		color: #a2a2a2;
	}
	.word_main li.item{
		margin: 0 8px;
		cursor: pointer;
	}
	.word_main li.item:hover{
		color: #b80000;
	}
	.keys{
		overflow: hidden;
	}
	.keys>li{
		float: left;
		display: block;
		background-color: #ccc;
		/*border: 1px solid #30333a;*/
		font-size: 15px;
		color: #30333a;
		padding: 4px 8px;
		border-radius: 5px;
		margin: 10px 5px;
		cursor: pointer;
	}
	.key2>li{
		background-color: #30333a;
		/*border: 1px solid #9195a0;*/
		color: #ccc;
	}
</style>



